const NoteFeed = props => {
  const notes = props.notes
  return(
    <NoteWrapper>
      {notes.map(note => (
        <div key={note.id}>
           <Note note={note} />
           <Link to={`note/${note.id}`}>Permalink</Link>
        </div>     
      ))}     
    </NoteWrapper>  
  )
}

export default NoteFeed